/* overide react-contexify */
.react-contexify {
	padding: 0;
	z-index: 99;
}
.react-contexify__item__data {
	border-left: 2px solid transparent;
	-webkit-transition: all 0.2s;
	-o-transition: all 0.2s;
	transition: all 0.2s;
	padding: 0;
	font-size: 0.9em;
	height: 40px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	padding-left: 10px;
}
.react-contexify__item__data .danger {
	width: 100%;
    height: 100%;
    position: absolute;
    left: -2px;
    padding-left: 10px;
    border-left: 2px solid transparent;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.react-contexify__item:not(.react-contexify__item--disabled):hover>.react-contexify__item__data {
	color: #4393e6;
	border-color: #4393e6;
	background-color: #eaeaea;
}
.react-contexify__item:not(.react-contexify__item--disabled):hover>.react-contexify__item__data .danger {
	color: red;
	border-color: red;
	background-color: #eaeaea;
}


