#loadingMask {
    position: fixed;
    top: 0px;
    left: 0px;
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: bloack;
}

.loadingMask-element {
    position: absolute;
    top: 40%;
    left: 50%;
    width: 70px;
    height: 70px;
    margin-left: -35px !important;
}

.loader-animation {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -35px 0 0 -35px;
    width: 70px;
    height: 70px;
}

.loader-animation::before {
    position: absolute;
    top: 50%;
    margin: -4px 0 0 0;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: rgba(111,148,182,0.3);
    content: '';
    opacity: 0;
    -webkit-transform-origin: 35px 50%;
    -ms-transform-origin: 35px 50%;
        transform-origin: 35px 50%;
    -webkit-animation-name: loader-anim-2, loader-anim-3;
    animation-name: loader-anim-2, loader-anim-3;
    -webkit-animation-duration: 8s, 0.4s;
    animation-duration: 8s, 0.4s;
    -webkit-animation-iteration-count: infinite, infinite;
    animation-iteration-count: infinite, infinite;
    -webkit-animation-timing-function: ease, linear;
    animation-timing-function: ease, linear;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
}

.loader-text {
    color: #efefef;
    font-size: 12px;
    text-align: center;
    position: absolute;
    top: 26px;
    left: 14px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.loader-animation::after {
    content: "";
    display: block;
    position: absolute;
    width: 70px;
    height: 70px;
    border: 8px solid rgba(111,148,182,0.1);
    border-radius: 50%;
    -webkit-animation: loader-anim-1 30s forwards;
    animation: loader-anim-1 30s forwards;
}

@-webkit-keyframes loader-anim-1 {
	0%, 100% {
		-webkit-transform: scale3d(0, 0, 1);
		transform: scale3d(0, 0, 1);
		opacity: 0;
	}
	1%, 99% {
		opacity: 1;
		-webkit-transform: scale3d(1, 1, 1);
		transform: scale3d(1, 1, 1);
	}
}

@keyframes loader-anim-1 {
	0%, 100% {
		-webkit-transform: scale3d(0, 0, 1);
		transform: scale3d(0, 0, 1);
		opacity: 0;
	}
	1%, 99% {
		opacity: 1;
		-webkit-transform: scale3d(1, 1, 1);
		transform: scale3d(1, 1, 1);
	}
}

@-webkit-keyframes loader-anim-2 {
	0%, 5%, 95%, 100% {
		opacity: 0;
	}
	10%, 90% {
		opacity: 1;
	}
}

@keyframes loader-anim-2 {
	0%, 5%, 95%, 100% {
		opacity: 0;
	}
	10%, 90% {
		opacity: 1;
	}
}

@-webkit-keyframes loader-anim-3 {
	100% {
		-webkit-transform: rotate3d(0, 0, 1, 360deg);
		transform: rotate3d(0, 0, 1, 360deg);
	}
}

@keyframes loader-anim-3 {
	100% {
		-webkit-transform: rotate3d(0, 0, 1, 360deg);
		transform: rotate3d(0, 0, 1, 360deg);
	}
}

