#customizeData, #publishData {
    -ms-flex: 1 1;
        flex: 1 1;
    overflow: auto;
    background-color: #F0F0F0;
}
.customzie-content, .publish-content {
    max-width: 1140px;
    margin: 20px auto 20px auto;
    display: -ms-flexbox;
    display: flex;
}
.settingsList {
    padding: 0 20px 30px 20px;
}
.settingsList.disabled {
    opacity: 0.3;
}
.settingsList.disabled input,
.settingsList.disabled textarea {
    outline: none;
    pointer-events:none;
}
.input-header {
    font-size: 15px;
    color: #2f2f2f;
    line-height: 14px;
    margin: 20px 0;
    padding: 0 10px;
}
.input-note {
    font-size: 12px;
    text-transform: lowercase;
    font-style: italic;
    margin-left: 5px;
}
.settingsList input,
.settingsList textarea {
    font-size: 17px;
    color: #252525;
    background: #f9fafa;
    border: 2px solid #f0f0f0;
    padding-left: 20px;
    width: 100%;
    min-height: 56px;
    border-radius: 2px;
    resize: vertical;
    padding: 10px;
}
.settingsListItem {
    display: -ms-flexbox;
    display: flex;
    min-height: 55px;
    -ms-flex-align: center;
        align-items: center;
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
}
.settingsListItem span:first-of-type {
    -ms-flex: 1 1;
        flex: 1 1;
}
.settingsListItem.border-top-less {
    border-top: 0px;
}
.st-size-item {
    display: -ms-flexbox;
    display: flex;
}
.st-size-item > h2 {
    font-size: 1em;
}
.st-size-item > h2:first-child {
    color: #555;
    min-width: 140px;
}
.settingsInfoText .st-size-item:first-child {
    margin-top: 20px;
}
/* PUBLIST */
.publish-content {
    -ms-flex-direction: column;
        flex-direction: column;
    border-radius: 4px;
    background-color: #fff;
    padding: 20px 0;
}
.publish-title {
    padding: 10px;
    background-color: #ccc;
    font-size: 18px;
}
.publish-title.sub {
    padding: 6px;
    background-color: #ebebeb;
    font-size: 16px;
    padding-left: 18px;
}
.publish-paragraph {
    padding: 5px;
    padding-left: 30px;
}
.publish-action {
    margin-top: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.publish-paragraph input {
    font-size: 16px;
    color: #252525;
    background: #f9fafa;
    border: 2px solid #f0f0f0;
    padding-left: 20px;
    width: 100%;
    height: 44px;
    border-radius: 2px;
}


